// eslint-disable no-parameter-reassign
import { createSlice } from '@reduxjs/toolkit';
import {
  createMessageBoardBinaryBlob2,
  createMessageBoardBinaryBlob3,
  saveFile,
  createJSONBlob,
} from '../../app/utils';
import { getPixelsOfLines } from './helpers';

export const DEFAULT_GRAPHICAL_TEXT = ' CAUTION ';

const slice = createSlice({
  name: 'scrollingBoard',
  initialState: {
    column: 36,
    firstLine: 'CAUTION',
    firstLineOffset: 0,
    imageData: [],
    isGraphical: false,
    ledColor: { r: 255, g: 0, b: 0, a: 1 },
    pixelCornerRadius: 5,
    pixelSize: 10,
    row: 8,
    secondLine: 'Line 1',
    scrollSpeed: 40,
    spaceBetweenPixels: null,
    style: {
      textColor: '#ff0000',
      fontSize: 8,
    },
  },
  reducers: {
    setColumn: (state, action) => {
      state.column = action.payload;
    },
    setImageData: (state, action) => {
      state.imageData = action.payload;
    },
    setIsGraphical: (state, action) => {
      state.isGraphical = action.payload;
    },
    setFirstLine: (state, action) => {
      state.firstLine = action.payload;
    },
    setLedColor: (state, action) => {
      state.ledColor = action.payload;
    },
    setPixelCornerRadius: (state, action) => {
      state.pixelCornerRadius = action.payload;
    },
    setRow: (state, action) => {
      state.row = action.payload;
    },
    setSecondLine: (state, action) => {
      state.secondLine = action.payload;
    },
    setScrollSpeed: (state, action) => {
      state.scrollSpeed = action.payload;
    },
    setBoardState: (state, action) => action.payload,
    exportDataLite: (state, action) => {
      if (state.isGraphical) {
        const [blob, type] = action.payload
          ? [createJSONBlob(state.imageData), 'json']
          : [
            createMessageBoardBinaryBlob2(
              state.imageData,
              state.column,
              state.row,
            ),
            'bin',
          ];
        saveFile(blob, `message-board-${new Date().getTime()}.${type}`);
      } else {
        const [blob, type] = action.payload
          ? [createJSONBlob(state.imageData), 'json']
          : [
            createMessageBoardBinaryBlob3(
              getPixelsOfLines(state),
              state.column,
              state.row,
              true,
            ),
            'bin',
          ];
        saveFile(blob, `message-board-${new Date().getTime()}.${type}`);
      }
    },
  },
});

export const {
  exportDataLite,
  setColumn,
  setFirstLine,
  setImageData,
  setIsGraphical,
  setLedColor,
  setRow,
  setSecondLine,
  setScrollSpeed,
  setBoardState,
} = slice.actions;

export const getBoardState = (state) => state.scrollingBoard2;
export const getColumn = (state) => state.scrollingBoard2.column;
export const getFirstLine = (state) => state.scrollingBoard2.firstLine;
export const getImageData = (state) => state.scrollingBoard2.imageData;
export const getIsGraphical = (state) => state.scrollingBoard2.isGraphical;
export const getLedColor = (state) => state.scrollingBoard2.ledColor;
export const getRow = (state) => state.scrollingBoard2.row;
export const getSecondLine = (state) => state.scrollingBoard2.secondLine;
export const getScrollSpeed = (state) => state.scrollingBoard2.scrollSpeed;

export default slice.reducer;
