import FileSaver from 'file-saver';
import { rgbToHex as convertToHex } from '@material-ui/core/styles';
import { TRANSPARENT_COLOR } from '../features/LightbarSchema';

const reorderLights = (lights) => [
  ...lights.slice(10, 16).reverse(),
  ...lights.slice(6, 10).reverse(),
  ...lights.slice(0, 6).reverse(),
  ...lights.slice(16).reverse(),
];

export const toHex = (value) => {
  let hex = Number(value).toString(16);
  if (hex.length < 2) {
    hex = `0${hex}`;
  }

  return hex;
};

export const rgbToHex = (value) => {
  const color = value || { r: 0, g: 0, b: 0 };
  return convertToHex(`rgb(${color.r}, ${color.g}, ${color.b})`).toUpperCase();
};

export const createBinaryBlob = (lightbar, cycles) => {
  if (!cycles || !cycles.length) {
    return null;
  }

  const lightLength = cycles[0].lights.length + (lightbar.isLinear ? 1 : 0);
  let length = lightbar.isLinear ? 2 : 3;

  cycles.forEach((c) => {
    length += c.repeat * lightLength * (lightbar.isLinear ? 1 : 3);
  });

  const buffer = new ArrayBuffer(length);
  const dv = new DataView(buffer);
  let index = 3;

  if(lightbar.isLinear) {
    dv.setUint8(0, 0);
    dv.setUint8(1, cycles.length);
    index = 2;
  } else {
    dv.setUint8(0, cycles.length);
    dv.setUint8(1, 0);
    dv.setUint8(2, lightLength);
  }


  cycles.forEach((c) =>
    [...new Array(+c.repeat).keys()].forEach(() =>
      // reorderLights(c.lights).forEach((l) => {
      c.lights.forEach((l, i) => {
        const { r, g, b, a } = l.color;
        const isUsable = [r, g, b].join(',') !== [180, 180, 180].join(',');
        const isOff = [r, g, b].join(',') === [1, 1, 1].join(',');
        let red;
        let blue;
        let green = 0;

        if (isOff) {
          red = 1;
          blue = 1;
          green = 1;
        } else {
          red = r !== 0 && g === 0 && b === 0 && isUsable ? a * 255 : (isUsable ? 1 : 0);
          blue = r === 0 && g === 0 && b !== 0 && isUsable ? a * 255 : (isUsable ? 1 : 0);
          green = isUsable ? (red > 1 || blue > 1 ? 1 : a * 255) : 0;
        }

        const msb = isUsable ? 128 : 0;
        const linearValue = [(msb + (isOff || !isUsable ? 0 : (a*100))).toFixed(0)];

        if(i === 20) {
          linearValue.push(0);
        }

        const output = lightbar.isLinear ?
          linearValue : [
            red.toFixed(0),
            green.toFixed(0),
            blue.toFixed(0),
          ];
        return output.forEach((v) => dv.setUint8(index++, v));
      }),
    ),
  );

  return new Blob([dv.buffer], { type: 'application/octet-stream' });
};

export const createMessageBoardBinaryBlob = (data) => {
  const buffer = new ArrayBuffer(
    Math.ceil(data.length / 8) * 8 * data[0].length * 3,
  );
  const dv = new DataView(buffer);

  // Reformats the data to be as a area of 8 * 16 pixels
  for (let i = 0; i < Math.ceil(data.length / 8); i += 1) {
    for (let r = 0; r < data[0].length; r += 1) {
      for (let c = 0; c < 8; c += 1) {
        const binaryIndex = i * 8 * data[0].length * 3 + r * 8 + c * 3;
        if (data[i * 8 + c]) {
          data[i * 8 + c][r].forEach((value, index) =>
            dv.setUint8(binaryIndex + index, value),
          );
        } else {
          [0, 0, 0].forEach((value, index) =>
            dv.setUint8(binaryIndex + index, value),
          );
        }
      }
    }
  }

  return new Blob([dv.buffer], { type: 'application/octet-stream' });
};

export const createMessageBoardBinaryBlob3 = (
  boardData,
  column = 48,
  row = 18,
  isLite = false,
) => {
  const [firstLine, secondLine] = boardData;
  let frames =
    secondLine && secondLine.pixels.length > column + 1 - 2
      ? secondLine.pixels.length + 1
      : 1;

  if (isLite) {
    frames =
      firstLine && firstLine.pixels.length > column + 1 - 2
        ? firstLine.pixels.length + 1
        : 1;
  }
  const buffer = new ArrayBuffer(frames * column * row * 3 + 4);
  const dv = new DataView(buffer);
  dv.setUint32(0, buffer.byteLength);

  // Reformats the data to be as a area of 8 * 16 pixels
  for (let f = 0; f < frames; f += 1) {
    for (let c = 0; c < column; c += 1) {
      for (let r = 0; r < row; r += 1) {
        const binaryIndex = f * column * row * 3 + c * row * 3 + r * 3 + 4;

        if ([0, 8, 9, 17].indexOf(r) > -1) {
          [0, 0, 0].forEach((value, index) =>
            dv.setUint8(binaryIndex + index, value),
          );
        } else if (r < 8) {
          if (isLite) {
            if (firstLine && firstLine.pixels[f + c]) {
              firstLine.pixels[f + c][r - 1].forEach((value, index) => {
                // console.log(binaryIndex, '-', index, '-', value);
                dv.setUint8(binaryIndex + index, value);
              });
            } else {
              [0, 0, 0].forEach((value, index) =>
                dv.setUint8(binaryIndex + index, value),
              );
            }
          } else if (firstLine && firstLine.pixels[c]) {
            firstLine.pixels[c][r - 1].forEach((value, index) => {
              // console.log(binaryIndex, '-', index, '-', value);
              dv.setUint8(binaryIndex + index, value);
            });
          } else {
            [0, 0, 0].forEach((value, index) =>
              dv.setUint8(binaryIndex + index, value),
            );
          }
        } else if (r > 9) {
          if (secondLine && secondLine.pixels[f + c]) {
            secondLine.pixels[f + c][r - 10].forEach((value, index) => {
              // console.log(binaryIndex, '-', index, '-', value);
              dv.setUint8(binaryIndex + index, value);
            });
          } else {
            [0, 0, 0].forEach((value, index) =>
              dv.setUint8(binaryIndex + index, value),
            );
          }
        }
      }
    }

    if (isLite) {
      if (!firstLine || firstLine.length <= column - 1) {
        break;
      }
    } else if (!secondLine || secondLine.length <= column - 1) {
      break;
    }
  }

  console.log(dv.buffer);

  return new Blob([dv.buffer], { type: 'application/octet-stream' });
};

