export const createJSONBlob = (lightbar) =>
  new Blob([JSON.stringify(lightbar)], { type: 'application/json' });

export const saveFile = (blob, name) => {
  FileSaver.saveAs(blob, name);
};

export const getDefaultLights = (lightbar, schema = []) =>
  [...Array(lightbar.total).keys()].map((key) => ({
    text: key + 1,
    color:
      (schema.at(key) || []).indexOf(TRANSPARENT_COLOR) > -1
        ? {
          r: 180,
          g: 180,
          b: 180,
          a: 1,
        }
        : {
            r: 1,
          g: 1,
          b: 1,
          a: 1,
        },
  }));
