import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core';
import { Stage, Layer, Rect, Text, Group } from 'react-konva';

import { rgbToHex } from '../../app/utils';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    height: (props) => (props.width ? 'auto' : 210),
    justifyContent: 'center',
    marginLeft: 10,
    maxWidth: 750,
    minWidth: (props) => props.width || 500,
    width: (props) => props.width || '100%',
  },
});

const Light = ({
  fill,
  getStrokeColor,
  height,
  onClick,
  overlay,
  rotation,
  scale,
  shadowBlur,
  stroke,
  text,
  width,
  x,
  y,
}) => (
  <>
    <Rect
      fill={fill}
      height={height}
      cornerRadius={5}
      onClick={onClick}
      rotation={rotation}
      shadowBlur={shadowBlur}
      stroke={stroke}
      width={width}
      x={x}
      y={y}
    />
    <Group rotation={rotation} x={x} y={y}>
      {overlay &&
        overlay.map((color, index) => (
          <Rect
            key={color}
            fill={color}
            height={height}
            cornerRadius={5}
            onClick={onClick}
            width={width / overlay.length}
            x={index * (width / overlay.length)}
            y={0}
          />
        ))}
    </Group>
    {text && (
      <Text
        align="center"
        fontSize={10 * scale}
        height={height}
        rotation={rotation}
        text={text}
        verticalAlign="middle"
        onClick={onClick}
        width={width}
        x={x}
        y={y + 25}
      />
    )}
  </>
);

function LightbarPreview({
  lights = [],
  overlays = [],
  onClick,
  selectedLight = [],
  title,
  width,
  isLinear = false,
  showLightLabel = false,
}) {
  const classes = useStyles({ width });
  const containerRef = useRef();

  if (!lights.length) {
    return null;
  }

  const front = isLinear ? lights : lights.slice(0, (lights.length - 6) / 2);
  const rear = lights.slice(front.length + 3, -3).reverse();
  const originalWidth = 57 * 2 + front.length * 45;
  const scale = (width || originalWidth) / originalWidth;
  const lightbarWidth = front.length * (45 * scale);
  const shadowBlur = 5 * scale;

  // TODO it doesn't work properly
  // useEffect(() => {
  //   if (containerRef.current.offsetWidth !== divWidth
  //     && containerRef.current.offsetWidth <= 750) {
  //       setDivWidth(containerRef.current.offsetWidth);
  //     }
  //   },
  // );

  const onLightClick = (id) => () => {
    onClick && onClick(id);
  };

  const getStrokeColor = (id) =>
    selectedLight.indexOf(id) > -1 ? 'red' : null;

  const getColor = ({ r, g, b, a }) =>
    rgbToHex({
      r: (r * a).toFixed(0),
      g: (g * a).toFixed(0),
      b: (b * a).toFixed(0),
    });

  return (
    <div ref={containerRef} className={classes.root}>
      <Stage width={originalWidth * scale} height={210 * scale}>
        <Layer x={57 * scale}>
          {front.map((l, index) => (
            <Light
              fill={getColor(l.color)}
              height={24 * scale}
              index={index}
              key={index}
              stroke={getStrokeColor(index)}
              text={showLightLabel && l.text}
              onClick={onLightClick(index)}
              overlay={overlays[index]}
              scale={scale}
              shadowBlur={shadowBlur}
              width={40 * scale}
              x={45 * index * scale}
              y={20 * scale}
            />
          ))}
        </Layer>
        { !isLinear &&
          <>
            <Layer x={57 * scale + lightbarWidth}>
              <Light
                x={12 * scale}
                y={25 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(lights[front.length].color)}
                text={showLightLabel && lights[front.length].text}
                shadowBlur={shadowBlur}
                rotation={40}
                cornerRadius={5}
                onClick={onLightClick(front.length)}
                overlay={overlays[front.length]}
                stroke={getStrokeColor(front.length)}
                scale={scale}
              />
              <Light
                x={53 * scale}
                y={70 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(lights[front.length + 1].color)}
                text={showLightLabel && lights[front.length + 1].text}
                shadowBlur={shadowBlur}
                rotation={90}
                cornerRadius={5}
                onClick={onLightClick(front.length + 1)}
                overlay={overlays[front.length + 1]}
                stroke={getStrokeColor(front.length + 1)}
                scale={scale}
              />
              {
                // <Light
                //   x={53 * scale}
                //   y={115 * scale}
                //   width={40 * scale}
                //   height={24 * scale}
                //   fill={getColor(lights[front.length + 2].color)}
                //   text={lights[front.length + 2].text}
                //   shadowBlur={shadowBlur}
                //   rotation={90}
                //   cornerRadius={5}
                //   onClick={onLightClick(front.length + 2)}
                //   overlay={overlays[front.length + 2]}
                //   stroke={getStrokeColor(front.length + 2)}
                //   scale={scale}
                // />
              }
              <Light
                x={-5 * scale}
                y={143 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(lights[front.length + 2].color)}
                text={showLightLabel && lights[front.length + 2].text}
                shadowBlur={shadowBlur}
                rotation={-40}
                cornerRadius={5}
                onClick={onLightClick(front.length + 2)}
                overlay={overlays[front.length + 2]}
                stroke={getStrokeColor(front.length + 2)}
                scale={scale}
              />
            </Layer>
            <Layer x={57 * scale}>
              {rear.map((l, index) => (
                <Light
                  fill={getColor(l.color)}
                  height={24 * scale}
                  index={index}
                  key={index}
                  stroke={getStrokeColor(lights.length - 4 - index)}
                  text={showLightLabel && l.text}
                  onClick={onLightClick(lights.length - 4 - index)}
                  overlay={overlays[lights.length - 4 - index]}
                  scale={scale}
                  shadowBlur={shadowBlur}
                  width={40 * scale}
                  x={45 * index * scale}
                  y={143 * scale}
                />
              ))}
            </Layer>
            <Layer visible={!isLinear}>
              <Light
                x={55 * scale}
                y={44 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(lights[lights.length - 1].color)}
                text={showLightLabel && lights[lights.length - 1].text}
                shadowBlur={shadowBlur}
                rotation={135}
                cornerRadius={5}
                onClick={onLightClick(lights.length - 1)}
                overlay={overlays[lights.length - 1]}
                stroke={getStrokeColor(lights.length - 1)}
                scale={scale}
              />
              <Light
                x={27 * scale}
                y={73 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(lights[lights.length - 2].color)}
                text={showLightLabel && lights[lights.length - 2].text}
                shadowBlur={shadowBlur}
                rotation={90}
                cornerRadius={5}
                onClick={onLightClick(lights.length - 2)}
                overlay={overlays[lights.length - 2]}
                stroke={getStrokeColor(lights.length - 2)}
                scale={scale}
              />
              {
                // <Light
                //   x={27 * scale}
                //   y={118 * scale}
                //   width={40 * scale}
                //   height={24 * scale}
                //   fill={getColor(lights[lights.length - 3].color)}
                //   text={lights[lights.length - 3].text}
                //   shadowBlur={shadowBlur}
                //   rotation={90}
                //   cornerRadius={5}
                //   onClick={onLightClick(lights.length - 3)}
                //   overlay={overlays[lights.length - 3]}
                //   stroke={getStrokeColor(lights.length - 3)}
                //   scale={scale}
                // />
              }
              <Light
                x={25 * scale}
                y={118 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(lights[lights.length - 3].color)}
                text={showLightLabel && lights[lights.length - 3].text}
                shadowBlur={shadowBlur}
                rotation={40}
                cornerRadius={5}
                onClick={onLightClick(lights.length - 3)}
                overlay={overlays[lights.length - 3]}
                stroke={getStrokeColor(lights.length - 3)}
                scale={scale}
              />
            </Layer>
          </>
        }
      </Stage>
    </div>
  );
}

LightbarPreview.propTypes = {
  lights: PropTypes.array,
  onClick: PropTypes.func,
  selectedLight: PropTypes.array,
  title: PropTypes.node,
  width: PropTypes.number,
  isLinear: PropTypes.bool,
  showLightLabel: PropTypes.bool,
};

export default LightbarPreview;
